@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/ptmono.woff2") format("woff2"), url("../fonts/ptmono.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensed.woff2") format("woff2"), url("../fonts/robotocondensed.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensedbold.woff2") format("woff2"), url("../fonts/robotocondensedbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  font-family: "PT Mono", "Lucida Console", monospace;
  color: #041827;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1320px;
  margin: 0 auto;
  padding: 0 50px;
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.btn {
  display: inline-flex;
  padding: 15px 25px;
  align-items: center;
  justify-content: center;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background-color: transparent;
  border: 1px solid transparent;
  border-radius: 3px;
  cursor: pointer;
  transition: all 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  color: #f2f5f6;
  background: linear-gradient(to bottom, #041827 85%, rgba(4, 24, 39, 0));
  z-index: 1;
}
@media (max-width: 1023px) {
  .header {
    position: absolute;
    z-index: 0;
  }
}

.header__container {
  padding: 30px 10px 35px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 30px;
}
@media (max-width: 1023px) {
  .header__container {
    padding: 20px 15px 25px 0;
    align-items: start;
  }
}
@media (max-width: 767px) {
  .header__container {
    padding: 10px 5px 15px 0;
  }
}

.header__logo {
  min-width: 215px;
  height: 55px;
}
@media (max-width: 767px) {
  .header__logo {
    min-width: 35px;
  }
}

.header__contacts-list {
  list-style-type: none;
  min-width: 460px;
  margin: -8px 0 0 -5px;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  column-gap: 20px;
  row-gap: 10px;
}
@media (max-width: 1023px) {
  .header__contacts-list {
    min-width: auto;
    margin-top: -5px;
    margin-left: 0;
    column-gap: 10px;
  }
}

.header__contacts-item {
  flex-grow: 1;
  display: flex;
  justify-content: start;
}
.header__contacts-item--tel {
  justify-content: end;
}
@media (max-width: 1439px) {
  .header__contacts-item--tel {
    justify-content: start;
  }
}

.header__button {
  min-width: 160px;
  min-height: 40px;
  margin: -5px 50px 0 0;
  padding: 10px 15px 10px;
  font-size: 14px;
  color: inherit;
  border-color: #f2f5f6;
}
@media (hover: hover) {
  .header__button:hover:not(.focus-visible) {
    background-color: #00b2ff;
    border-color: #00b2ff;
  }
}
.header__button.focus-visible:focus {
  background-color: #00b2ff;
  border-color: #00b2ff;
}
@media (max-width: 1023px) {
  .header__button {
    display: none;
  }
}

.header-link {
  line-height: 18px;
  color: inherit;
  display: grid;
  grid-template-columns: 20px auto;
  gap: 15px;
  align-items: center;
  justify-items: start;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .header-link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.header-link.focus-visible:focus {
  opacity: 0.8;
}
@media (max-width: 1023px) {
  .header-link {
    padding: 15px;
    display: flex;
  }
}

@media (max-width: 1023px) {
  .header-link__name {
    display: none;
  }
}

.intro {
  color: #f2f5f6;
  background-color: #041827;
  background-image: url("../../img/main-bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media (max-width: 1023px) {
  .intro {
    background-image: url("../../img/main-bg-tablet.jpg");
  }
}
@media (max-width: 767px) {
  .intro {
    background-image: url("../../img/main-bg-mobile.jpg");
  }
}
@media (min-resolution: 144dpi) and (max-width: 1023px), (min-resolution: 1.5dppx) and (max-width: 1023px) {
  .intro {
    background-image: url("../../img/main-bg-tablet@2x.jpg");
  }
}
@media (min-resolution: 144dpi) and (max-width: 767px), (min-resolution: 1.5dppx) and (max-width: 767px) {
  .intro {
    background-image: url("../../img/main-bg-mobile@2x.jpg");
  }
}
@media (min-resolution: 144dpi) and (max-width: 1919px), (min-resolution: 1.5dppx) and (max-width: 1919px) {
  .intro {
    background-image: url("../../img/main-bg@2x.jpg");
  }
}

.intro__container {
  min-height: 880px;
  padding: 305px 125px 150px;
}
@media (max-width: 1023px) {
  .intro__container {
    min-height: 640px;
    padding: 180px 0 150px;
  }
}
@media (max-width: 767px) {
  .intro__container {
    min-height: 560px;
    padding-top: 150px;
  }
}

.intro__title {
  margin: 0 0 32px 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  text-transform: uppercase;
  letter-spacing: 0.07em;
}
@media (max-width: 1023px) {
  .intro__title {
    margin-bottom: 0;
    font-size: 64px;
    line-height: 75px;
  }
}
@media (max-width: 767px) {
  .intro__title {
    margin-bottom: 5px;
    font-size: 36px;
    line-height: 43px;
  }
}

.intro__description {
  max-width: 685px;
  margin: 0 0 47px 0;
  font-size: 22px;
  line-height: 34px;
}
@media (max-width: 1023px) {
  .intro__description {
    margin-bottom: 58px;
  }
}
@media (max-width: 767px) {
  .intro__description {
    max-width: 300px;
    margin-bottom: 46px;
    font-size: 14px;
    line-height: 22px;
  }
}

.intro__button {
  margin-bottom: 40px;
  font-size: 18px;
  line-height: 21px;
  color: #f2f5f6;
  background-color: #00b2ff;
  border-color: #00b2ff;
  text-align: center;
}
.intro__button::before {
  width: 100%;
  height: 100%;
  content: "Получить бесплатную консультацию";
}
@media (max-width: 1023px) {
  .intro__button {
    padding: 15px 25px;
    margin-left: 45px;
  }
}
@media (max-width: 767px) {
  .intro__button {
    padding: 10px 30px;
    min-width: 100%;
    margin-left: 0;
    font-size: 14px;
    line-height: 16px;
  }
  .intro__button::before {
    width: 100%;
    height: 100%;
    content: "Бесплатная консультация";
  }
}
@media (hover: hover) {
  .intro__button:hover:not(.focus-visible) {
    background-color: #041827;
    border-color: #f2f5f6;
  }
}
.intro__button.focus-visible:focus {
  background-color: #041827;
  border-color: #f2f5f6;
}

.advantages {
  background-color: #f8fdff;
}

.advantages__container {
  padding: 120px 0 60px 0;
}
@media (max-width: 1023px) {
  .advantages__container {
    padding: 80px 0 40px;
  }
}
@media (max-width: 767px) {
  .advantages__container {
    padding: 50px 0 25px;
  }
}

.advantages__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
}
@media (max-width: 767px) {
  .advantages__list {
    grid-template-columns: 1fr;
    gap: 25px;
  }
}

.advantage-card {
  max-width: 595px;
  min-height: 300px;
  padding: 37px 42px;
  display: flex;
  flex-direction: column;
  border: 1px solid #ebebeb;
  border-left: 5px solid #00b2ff;
  transition: all 0.2s ease-in;
}
@media (max-width: 1023px) {
  .advantage-card {
    min-height: 360px;
    padding: 40px 40px 20px;
  }
}
@media (max-width: 767px) {
  .advantage-card {
    min-height: 325px;
    padding: 25px 35px 55px 25px;
  }
}
@media (hover: hover) {
  .advantage-card:hover:not(.focus-visible) {
    background-color: #00b2ff;
    border-color: #00b2ff;
    color: #f2f5f6;
  }
}

.advantage-card__icon {
  margin: 0 0 20px 0;
  color: #00b2ff;
  transition: color 0.2s ease-in;
}
@media (max-width: 1023px) {
  .advantage-card__icon {
    margin-bottom: 12px;
  }
}
@media (max-width: 767px) {
  .advantage-card__icon {
    margin-bottom: 20px;
  }
}

.advantage-card:hover .advantage-card__icon {
  color: #f2f5f6;
}

.advantage-card h3 {
  margin: 0 0 15px 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 34px;
  color: #00b2ff;
  transition: color 0.2s ease-in;
}
@media (max-width: 1023px) {
  .advantage-card h3 {
    margin-bottom: 7px;
  }
}
@media (max-width: 767px) {
  .advantage-card h3 {
    margin-bottom: 17px;
    font-size: 20px;
    line-height: 22px;
  }
}

.advantage-card:hover h3 {
  color: #f2f5f6;
}

.advantage-card p {
  margin: 0 0 15px 0;
}
@media (max-width: 1023px) {
  .advantage-card p {
    margin-bottom: 10px;
  }
}
@media (max-width: 767px) {
  .advantage-card p {
    font-size: 14px;
    line-height: 19px;
  }
}

.section-title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 50px;
}

.products {
  background-color: #041827;
}

.products__container {
  padding: 66px 0 130px;
  display: flex;
  flex-direction: column;
}
@media (max-width: 1023px) {
  .products__container {
    align-items: center;
    padding: 57px 0 66px;
  }
}
@media (max-width: 767px) {
  .products__container {
    padding: 47px 0 76px;
  }
}

.products__title {
  margin: 0 0 54px 0;
  color: #ffffff;
}
.products__title::before {
  width: 100%;
  height: 100%;
  content: "Smart Device предлагает следующие товары и услуги";
}
@media (max-width: 1023px) {
  .products__title {
    margin-bottom: 37px;
  }
}
@media (max-width: 767px) {
  .products__title {
    margin-bottom: 28px;
    font-size: 28px;
    line-height: 40px;
  }
  .products__title::before {
    width: 100%;
    height: 100%;
    content: "Товары и услуги Smart Device";
  }
}
@media (max-width: 374px) {
  .products__title {
    max-width: 200px;
    align-self: start;
  }
}

.products__list {
  list-style-type: none;
  margin: 0 0 10px 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 85px;
}
@media (max-width: 1023px) {
  .products__list {
    grid-template-columns: 1fr;
    gap: 30px;
  }
}

.product-card {
  max-width: 350px;
  min-height: 650px;
  background-color: #12476b;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.product-card--production {
  background-image: url("../../img/production-bg.jpg");
}
.product-card--installation {
  background-image: url("../../img/installation-bg.jpg");
}
.product-card--lighting-details {
  background-image: url("../../img/lighting-details-bg.jpg");
}
@media (max-width: 1023px) {
  .product-card {
    min-width: 670px;
    min-height: 250px;
  }
  .product-card--production {
    background-image: url("../../img/production-bg-tablet.jpg");
  }
  .product-card--installation {
    background-image: url("../../img/installation-bg-tablet.jpg");
  }
  .product-card--lighting-details {
